import clsx from 'clsx';
import React, { useEffect } from 'react';
import { analyticsService } from '@wk/acm-analytics';

import styles from './search-for-help-hint-content.scss';

const SearchForHelpHintContentComponent: React.FC = () => {
    useEffect(() => {
        analyticsService.onWidgetEvent({
            widgetId: 'helpPopup',
            action: 'open',
            subType: 'popup',
        });
    }, []);

    return (
        <div className={styles['search-for-help-content']}>
            <h4
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                Advanced Search Techniques
            </h4>
            <p className={styles['search-for-help-content_margin-bottom-29']}>
                <i>Word Searches:</i> You may enter a single word, multiple words, quoted phrases, or use Boolean
                operators to help you find special language used in filings. Word searches may be used in conjunction
                with any other search criteria on the search page.
            </p>
            <h4
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                Boolean Operators
            </h4>
            <ul
                className={clsx(
                    styles['search-for-help-content_margin-bottom-29'],
                    styles['search-for-help-content_list']
                )}>
                <li className={styles['search-for-help-content_list-item']}>Grouping</li>
                <li className={styles['search-for-help-content_list-item']}>Inclusions</li>
                <li className={styles['search-for-help-content_list-item']}>Exclusions</li>
                <li className={styles['search-for-help-content_list-item']}>Proximity</li>
                <li className={styles['search-for-help-content_list-item']}>Quotations and Phrases</li>
                <li className={styles['search-for-help-content_list-item']}>Mixing Operators</li>
                <li className={styles['search-for-help-content_list-item']}>Stemming</li>
            </ul>
            <h4
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                Grouping - ( ) Parenthesis:{' '}
                <span className={styles['search-for-help-content_text-green']}>(x or y) and z</span>
            </h4>
            <p
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                ex. <span className={styles['search-for-help-content_text-green']}>(Agreement or Plan) and Stock</span>
            </p>
            <p className={styles['search-for-help-content_margin-bottom-29']}>
                Rule: The parenthesis denotes a group. A group will be processed before any other attached expression.
                The results of the group will then be processed with the exterior expression
            </p>
            <h4
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                Inclusions - AND operator:
                <span className={styles['search-for-help-content_text-green']}> &apos;x&apos; and &apos;y&apos;</span>
            </h4>
            <p
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                ex. <span className={styles['search-for-help-content_text-green']}>Oil AND Gas = oil+gas</span>
            </p>
            <p className={styles['search-for-help-content_margin-bottom-29']}>
                Rule: The AND operator will search for both occurrences of &apos;x&apos; and &apos;y&apos; in each
                entire filing. This will not assure close proximity, but will assure that both words occurred in the
                returned document at least once.
            </p>
            <h4
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                Exclusions - NOT:{' '}
                <span className={styles['search-for-help-content_text-green']}>&apos;x&apos; not &apos;y&apos;</span>
            </h4>
            <p
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                ex. <span className={styles['search-for-help-content_text-green']}>William H. Gates NOT Microsoft</span>
            </p>
            <p className={styles['search-for-help-content_margin-bottom-29']}>
                The NOT operator will search for the occurrence of &apos;x&apos; but will reject all filings that also
                include the word &apos;y&apos;. In the example above, all filings that mention William H. Gates,
                excluding those that contain Microsoft, will be displayed.
            </p>
            <h4
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-10']
                )}>
                Proximity
            </h4>
            <p className={styles['search-for-help-content_margin-bottom-14']}>
                - w/#: # = number of words{' '}
                <span
                    className={clsx(
                        styles['search-for-help-content_text-green'],
                        styles['search-for-help-content_text-bold']
                    )}>
                    X w/# Y
                </span>
            </p>
            <p
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                ex. <span className={styles['search-for-help-content_text-green']}>Background w/5 merger</span>
            </p>
            <p
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                ex. <span className={styles['search-for-help-content_text-green']}>Bylaws w/10 exhibit</span>
            </p>
            <p className={styles['search-for-help-content_margin-bottom-14']}>
                The w/# operator will search for all occurrences of &apos;x&apos; that are within &apos;#&apos; words on
                either side of &apos;y&apos;.
            </p>
            <p className={styles['search-for-help-content_margin-bottom-14']}>
                - f/#: # = number of words{' '}
                <span
                    className={clsx(
                        styles['search-for-help-content_text-green'],
                        styles['search-for-help-content_text-bold']
                    )}>
                    X f/# Y
                </span>
            </p>
            <p
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                ex. <span className={styles['search-for-help-content_text-green']}>Bank f/2 America</span>
            </p>
            <p className={styles['search-for-help-content_margin-bottom-29']}>
                The f/# operator will search for all occurrences of &apos;y&apos; that are within &apos;#&apos; words
                AFTER &apos;x&apos;.
            </p>
            <h4
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                Quotations and Phrases:
            </h4>
            <p
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                ex. <span className={styles['search-for-help-content_text-green']}>&quot;Phase III&quot;</span>
            </p>
            <p className={styles['search-for-help-content_margin-bottom-29']}>
                When placing two words within quotes you are telling the search engine to find the exact phrase. This
                would be the same if you were to submit{' '}
                <span className={styles['search-for-help-content_text-bold']}>Phase</span> f/1(1)
                <span className={styles['search-for-help-content_text-bold']}> III</span>.
            </p>
            <h4
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-10']
                )}>
                Mixing Operators:
            </h4>
            <p className={styles['search-for-help-content_margin-bottom-14']}>
                You can mix and match operators. Here are a couple examples:
            </p>
            <p
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                ex.{' '}
                <span className={styles['search-for-help-content_text-green']}>
                    &quot;Proved Reserves&quot; and Oil
                </span>
            </p>
            <p className={styles['search-for-help-content_margin-bottom-14']}>
                This would find all the filings that had the &quot;Proved Reserves&quot; exact phrase and also contained
                the word &quot;Oil&quot; somewhere within the document.
            </p>
            <p
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                ex. <span className={styles['search-for-help-content_text-green']}>Oil w/10 tanker not ocean</span>
            </p>
            <p className={styles['search-for-help-content_margin-bottom-29']}>
                This would find all the filings that had the word &quot;Oil&quot; within 10 words (before or after) of
                the word &quot;tanker&quot;. It would then scan these results and exclude all documents that contained
                the word &quot;ocean&quot;.
            </p>
            <h4
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                Stemming: - * Asterisk: <span className={styles['search-for-help-content_text-green']}>x *</span>
            </h4>
            <p
                className={clsx(
                    styles['search-for-help-content_text-bold'],
                    styles['search-for-help-content_margin-bottom-14']
                )}>
                ex. <span className={styles['search-for-help-content_text-green']}>Litigat *</span>
            </p>
            <p>
                Rule: The asterisk denotes truncation. In the example above, the search will look for all words
                beginning with the letters &quot;litigat&quot; -- e.g., litigate, litigating, litigation, litigator,
                etc.
            </p>
        </div>
    );
};

export const SearchForHelpHintContent = React.memo(SearchForHelpHintContentComponent);
